body {
  max-width: 1040px;
  margin: auto;
}
form {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  height: 100vh;
}
input {
  margin-bottom: 20px;
}
label {
  margin-bottom: 5px;
}
button {
  transition: ease-in 0.3s;
  border: 1px solid #4687ff;
  background-color: #4687ff;
  color: #ffffff;
  cursor: pointer;
  &:hover {
    background-color: #77a7ff;
    border-color: #77a7ff;
  }
  &:disabled {
    background-color: gray;
    border-color: gray;
    cursor: not-allowed;
  }
}
label, button {
  font-weight: 700;
}
button, input {
  border-radius: 3px;
  padding: .8rem 1.8rem;
}
.bad-input input {
  border: 1px solid red;
}
p {
  margin-top: 0;
  color: red;
}